import { DocStyle, environment } from '@ali/4ever-cangjie';
import { Fonts as FontsMark } from '@ali/4ever-plugin-marks';
import { fontSize, SzUtil, fontRenderFallback } from '@ali/4ever-utils';
import {  } from '@ali/4ever-utils';

export default function getTextStyle(docStyle?: DocStyle) {
  const style: React.CSSProperties = {};
  const isMobile = environment.IS_MOBILE;
  if (!docStyle?.docDefaults && !isMobile) return style;

  const { rPr } = docStyle?.docDefaults?.data || {};
  if (rPr?.sz) {
    let sz = SzUtil.toPt(rPr.sz, rPr.szUnit || 'px');
    if (isMobile) {
      // 移动端统一放大
      sz *= SzUtil.FONTSIZE_BIGGER;
    }
    style.fontSize = `${sz}pt`;
  } else if (isMobile) {
    style.fontSize = `${fontSize.DEFAULT * SzUtil.FONTSIZE_BIGGER}pt`;
  }

  if (rPr?.fonts) {
    style.fontFamily = fontRenderFallback.getFontFallback(FontsMark.create(rPr.fonts));
  }
  return style;
}
